import React from 'react'
import { Checkbox } from 'antd'
import { ColorsStyled } from './Domains.styled'
import { LegendStyled } from './Legend.styled'
import { LinkToToggleOptionsStyled } from './Options.styled'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'
import { faAngleDown, faAngleUp } from '@fortawesome/pro-regular-svg-icons'

const Legend = function(props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * View
   */
  return (
    <LegendStyled
      className={
        'LegendStyled' + (props.className ? ' ' + props.className : '')
      }
    >
      <LinkToToggleOptionsStyled
        className="LinkToToggleOptionsStyled"
        onClick={that.toggleOptions}
      >
        <span>{that.state.showOptions ? 'hide' : 'more'}&nbsp;</span>
        <span className="hide-small">API&nbsp;</span>
        <span>options&nbsp;</span>
        {that.state.showOptions ? (
          <FA icon={faAngleUp} className="faAngleUp" />
        ) : (
          <FA icon={faAngleDown} className="faAngleDown" />
        )}
      </LinkToToggleOptionsStyled>

      <ColorsStyled className="ColorsStyled">
        <span className="label show-large">show: </span>
        <span className="domname status1">
          <Checkbox
            className="Checkbox"
            size="small"
            checked={!that.props.hide_available}
            onChange={() => {
              that.toggleResults('available')
            }}
          />
          <span className="symbol">$</span>
          <span className="text show-large">available</span>
        </span>
        <span className="domname status2">
          <Checkbox
            className="Checkbox"
            size="small"
            checked={!that.props.hide_premium}
            onChange={() => {
              that.toggleResults('premium')
            }}
          />
          <span className="symbol">$$</span>
          <span className="text show-large">premium</span>
        </span>
        {/*<span className="domname status9">*/}
        {/*  <Checkbox*/}
        {/*    className="Checkbox"*/}
        {/*    size="small"*/}
        {/*    checked={!that.props.hide_other}*/}
        {/*    onChange={() => {*/}
        {/*      that.toggleResults('other')*/}
        {/*    }}*/}
        {/*  />*/}
        {/*  <span className="symbol">?</span>*/}
        {/*  <span className="text show-large">other</span>*/}
        {/*</span>*/}
        <span className="domname status10">
          <Checkbox
            className="Checkbox"
            size="small"
            checked={!that.props.hide_unavailable}
            onChange={() => {
              that.toggleResults('unavailable')
            }}
          />
          <span className="symbol show-small">n/a</span>
          <span className="symbol show-large">not available</span>
        </span>
        <span className="domname status0" style={{ opacity: 0.9 }}>
          <Checkbox
            style={{ opacity: 0.8 }}
            className="Checkbox"
            size="small"
            checked={!that.props.hide_other}
            onChange={() => {
              that.toggleResults('other')
            }}
          />
          <span className="symbol">&ndash;</span>
          <span className="symbol show-large">unknown</span>
        </span>
      </ColorsStyled>
    </LegendStyled>
  )
}

export default Legend
